import { Component, OnInit, Injectable, Inject, Renderer2, ElementRef } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef} from "@angular/material/dialog";
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngrx/store';

@Component({
  selector: 'app-user-details',
  templateUrl: './user-details.component.html',
  styleUrls: ['./user-details.component.scss']
})
export class UserDetailsComponent implements OnInit {
  details:any;
  data:any;
  dialog:any
  constructor( 
    @Inject(MAT_DIALOG_DATA) private _data, 
    private _dialog:MatDialogRef<any>, 
    private _http:HttpClient,
    private _renderer:Renderer2,
    private el:ElementRef,
     private _store:Store<any>
  ) {
      this.details = _data;
      this.dialog = this._dialog
      // console.log('Asasa=======================>',this._renderer.appendChild(this.el.nativeElement,'<p>HI I am Element!</p>'));
      this._store.dispatch({ type:'[USER] Load User'});
      this._store.subscribe(res=>{
          this.data = Object.values(res.users);
          console.log(this.data)
      });
   }

  ngOnInit() {
    // this.dialog.close(
      
    // )
  }

  getDetails(id){         
    try {
      this.details = this.data[(id-1)]
    } catch (error) {
      alert('No Data exits!')
    }        
  }




}
